"use client"
import Image from "next/image"
import Link from "next/link"
import { usePathname } from "next/navigation"
import { useEffect, useState } from "react"
import { ArrowUp } from "lucide-react" // ✅ add this at the top of the file

const nav = [
  { href: "/contact", label: "Contact us" },
  { href: "/faqs", label: "FAQs" },
  { href: "/blog", label: "Blog" },
]

export default function Navbar() {
  const pathname = usePathname()
  const [showButton, setShowButton] = useState(false)

  // ✅ Show scroll-to-top button after scrolling down
  useEffect(() => {
    const handleScroll = () => {
      setShowButton(window.scrollY > 300)
    }

    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  // ✅ Scroll to top smoothly
  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" })
  }

  return (
    <>
      {/* ✅ Fixed Navbar */}
      <header className="fixed top-0 left-0 right-0 z-50 bg-white/90 backdrop-blur-sm border-b border-gray-200 shadow-sm">
        <div className="mx-auto max-w-7xl px-8 py-3 flex items-center justify-between">
          {/* ✅ Left: Logo (with smooth scroll + animation) */}
          <div
            onClick={scrollToTop}
            className="flex items-center cursor-pointer transition-transform duration-300 hover:scale-105"
          >
            <Image
              src="/logo.png"
              alt="Workly"
              width={120}
              height={40}
              priority
              className="object-contain"
            />
          </div>

          {/* ✅ Right: Navigation Links */}
          <nav className="flex items-center text-m text-gray-800 font-semibold">
            {nav.map((item, index) => (
              <div key={item.href} className="flex items-center">
                <Link
                  href={item.href}
                  className={`transition-colors duration-200 hover:text-black ${
                    pathname === item.href ? "text-black" : "text-gray-800"
                  }`}
                >
                  {item.label}
                </Link>

                {/* Separator dot between links */}
                {index < nav.length - 1 && (
                  <span className="mx-3 text-gray-400 select-none">·</span>
                )}
              </div>
            ))}
          </nav>
        </div>
      </header>

      {/* ✅ Spacer below Navbar */}
      <div className="h-[40px] mb-2" />

      {/* ✅ Scroll-to-Top Button (modern + transparent + smooth) */}
     {showButton && (
  <button
    onClick={scrollToTop}
    className={`fixed bottom-6 right-6 z-50 flex items-center justify-center rounded-full 
    backdrop-blur-md bg-gray-100/60 border border-gray-300/30 shadow-lg 
    text-gray-800 transition-all duration-300 hover:bg-gray-200/80 hover:shadow-xl 
    cursor-pointer
    ${showButton ? "opacity-100 translate-y-0" : "opacity-0 translate-y-6 pointer-events-none"}`}
    style={{ width: 50, height: 50 }}
    aria-label="Scroll to top"
  >
    {/* ✅ Modern Arrow Icon */}
    <ArrowUp size={22} strokeWidth={2.5} />
  </button>
)}
    </>
  )
}
