"use client"
import { motion } from "framer-motion"
import {
  UserPlus,
  Settings,
  ShieldCheck,
  CreditCard,
  MapPin,
  Briefcase,
} from "lucide-react"

const steps = [
  {
    icon: <UserPlus className="w-7 h-7 text-sky-600" />,
    title: "Create Your Account",
    desc: "Sign up easily with your email or phone number to access the Workly platform and begin your journey.",
  },
  {
    icon: <Settings className="w-7 h-7 text-sky-600" />,
    title: "Set Up Your Profile",
    desc: "Add your skills, experience, and services. Personalize your profile to attract local clients quickly.",
  },
  {
    icon: <ShieldCheck className="w-7 h-7 text-sky-600" />,
    title: "Complete Verification",
    desc: "Upload identification documents for a smooth and secure onboarding experience.",
  },
  {
    icon: <CreditCard className="w-7 h-7 text-sky-600" />,
    title: "Pay Activation Fee",
    desc: "A small one-time fee helps maintain quality and safety standards across the platform.",
  },
  {
    icon: <MapPin className="w-7 h-7 text-sky-600" />,
    title: "Choose Your Service Area",
    desc: "Pick the areas you’d like to serve and set your own working hours for complete flexibility.",
  },
  {
    icon: <Briefcase className="w-7 h-7 text-sky-600" />,
    title: "Start Receiving Jobs",
    desc: "Once approved, receive job requests, choose what fits your schedule, and start earning instantly.",
  },
]

export default function Steps() {
  return (
    <section id="get-started" className="relative py-28 bg-white overflow-hidden">
      {/* ✅ Ambient gradient glow */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 0.2 }}
        transition={{ duration: 1.5 }}
        className="absolute -top-24 right-1/3 w-[500px] h-[500px] bg-sky-400/30 blur-[120px] rounded-full pointer-events-none"
      />

      <div className="relative mx-auto max-w-6xl px-6">
        {/* ✅ Section Heading */}
        <motion.div
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          viewport={{ once: true }}
          className="text-center max-w-2xl mx-auto"
        >
          <h2 className="text-4xl md:text-5xl font-extrabold text-gray-900">
            Getting Started with{" "}
            <span className="text-transparent bg-clip-text bg-gradient-to-r from-sky-600 to-blue-700">
              Workly
            </span>
          </h2>
          <p className="mt-4 text-gray-600 text-lg leading-relaxed">
            Set up your Workly profile in minutes — follow these simple steps to start offering your services and earning on your terms.
          </p>
        </motion.div>

        {/* ✅ Steps Grid */}
        <div className="mt-20 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-12 relative">
          {/* connector line for larger screens */}
          <div className="hidden lg:block absolute top-28 left-1/2 -translate-x-1/2 w-[75%] h-[2px] bg-gradient-to-r from-sky-200 via-blue-200 to-sky-200 opacity-70"></div>

          {steps.map((step, i) => (
            <motion.div
              key={step.title}
              initial={{ opacity: 0, y: 60 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{
                duration: 0.8,
                delay: i * 0.15,
                ease: "easeOut",
              }}
              viewport={{ once: true }}
              whileHover={{
                scale: 1.06,
                rotate: 0.3,
                transition: { duration: 0.4 },
              }}
              className="relative flex flex-col items-center text-center bg-white rounded-3xl border border-gray-100 p-10 shadow-sm hover:shadow-2xl transition-all duration-300 group"
            >
              {/* ✅ Floating step number */}
              <div className="absolute -top-6 left-1/2 -translate-x-1/2 bg-white border border-sky-100 rounded-full w-12 h-12 flex items-center justify-center font-bold text-sky-600 shadow-md group-hover:bg-sky-50 transition">
                {i + 1}
              </div>

              {/* ✅ Icon with pulse hover */}
              <motion.div
                whileHover={{ scale: 1.15, rotate: 3 }}
                transition={{ type: "spring", stiffness: 250 }}
                className="flex items-center justify-center h-16 w-16 mb-6 rounded-2xl bg-gradient-to-br from-sky-50 to-blue-50 group-hover:from-sky-100 group-hover:to-blue-100 transition-all"
              >
                {step.icon}
              </motion.div>

              {/* ✅ Step Title */}
              <h3 className="text-lg font-semibold text-gray-900 tracking-tight">
                {step.title}
              </h3>

              {/* ✅ Step Description */}
              <p className="mt-3 text-gray-600 text-sm leading-relaxed max-w-xs">
                {step.desc}
              </p>

              {/* ✅ Decorative bottom bar */}
              <motion.div
                initial={{ scaleX: 0 }}
                whileInView={{ scaleX: 1 }}
                transition={{ duration: 0.8, delay: 0.3 }}
                viewport={{ once: true }}
                className="mt-6 w-16 h-[3px] bg-gradient-to-r from-sky-500 to-blue-600 rounded-full origin-left"
              />
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  )
}
