"use client"
import { motion } from "framer-motion"
import { Sparkles, DollarSign, TrendingUp } from "lucide-react"

const items = [
  {
    icon: <Sparkles className="w-7 h-7 text-sky-600" />,
    title: "Be Your Own Boss",
    desc: "Work how, when, and where you want. Offer services in 50+ categories and set a flexible schedule and work area.",
  },
  {
    icon: <DollarSign className="w-7 h-7 text-sky-600" />,
    title: "Set Your Own Rates",
    desc: "Keep 100% of what you charge, plus tips! Invoice and get paid directly through our secure payment system.",
  },
  {
    icon: <TrendingUp className="w-7 h-7 text-sky-600" />,
    title: "Grow Your Business",
    desc: "We connect you with clients in your area, and ways to market yourself — so you can focus on what you do best.",
  },
]

export default function Features() {
  return (
    <section className="py-24 bg-white overflow-hidden">
      <div className="mx-auto max-w-6xl px-6">
        {/* ✅ Heading */}
        <motion.div
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          viewport={{ once: true }}
          className="text-center max-w-3xl mx-auto"
        >
          <h2 className="text-3xl sm:text-4xl font-bold leading-tight text-gray-1000  ">
            Flexible Work, At Your Fingertips
          </h2>
          <p className="mt-4 text-gray-600 text-lg leading-relaxed">
            Find local jobs that match your skills and availability. With{" "}
            <span className="font-semibold text-sky-600">Workly</span>, 
            you have the freedom to choose your schedule and build your income — your way.
          </p>
        </motion.div>

        {/* ✅ Features Grid */}
        <div className="mt-16 grid grid-cols-1 sm:grid-cols-3 gap-10 text-center">
          {items.map((it, i) => (
            <motion.div
              key={it.title}
              initial={{ opacity: 0, y: 60 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{
                duration: 0.8,
                delay: i * 0.2,
                ease: "easeOut",
              }}
              viewport={{ once: true }}
            >
              {/* ✅ Modern Card with light hover lift */}
              <motion.div
                whileHover={{ scale: 1.05, y: -6 }}
                transition={{ type: "spring", stiffness: 200, damping: 10 }}
                className="group bg-white rounded-2xl border border-gray-100 shadow-sm hover:shadow-xl transition-all duration-300 p-8"
              >
                <div className="flex items-center justify-center mx-auto mb-6 h-16 w-16 rounded-xl bg-sky-50 group-hover:bg-sky-100 transition">
                  {it.icon}
                </div>

                <h3 className="text-xl font-semibold text-gray-900">
                  {it.title}
                </h3>
                <p className="mt-3 text-gray-600 text-sm leading-relaxed max-w-xs mx-auto">
                  {it.desc}
                </p>
              </motion.div>
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  )
}
